@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
.home{
    padding:20px;
}
.all-flat{
    box-sizing: border-box;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 1fr;
    border-radius: 25px;
    width: 100%;
    padding:30px;
    gap: 30px;
    background-color: #F6F6F6;
}
.single-flat-image-container{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.single-flat{
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    box-sizing: border-box;
    border-radius: 25px;
    text-decoration: none;  
}
.single-flat-link{
    height: 100%;
}
.single-flat-img{
    width: 100%;
    height: 100%;
    border-radius: 25px;
    object-fit:cover;
    object-position: 15% 100%;
}
.single-flat::before{
    position: absolute;
    content: " ";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: linear-gradient(0deg, black, transparent);
    opacity: 0.4;
    border-radius: 25px;
}
.single-flat-title{
    position: absolute;
    bottom :10px;
    left:16px;
    text-decoration: none;
    color: rgb(255, 255, 255); /*change to white later*/
    font-size: 18px;
    font-family: Montserrat, sans-serif;
    font-weight: 500;
}

@media screen and (max-width: 768px) {
    .all-flat{
        grid-template-columns: repeat(1, 1fr);
    }
}

@media screen and (min-width:768px)and (max-width:992px) {
    .all-flat{
        grid-template-columns: repeat(2, 1fr);
    }
}
  