@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.banner{
    margin-top: 40px;
    margin-bottom: 40px;
    position:relative;
    text-align: center;
    height: 223px;
    background-image: url("../../image/cliff.jpg");
    border-radius: 25px;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;  
    background-position: 45% 45%;
}
.banner::before{
    position: absolute;
    content: " ";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.3);
    border-radius: 25px;
}
.banner-title{
    position: relative;
    text-align: center;
    font-size: 48px;
    color: white;
    font-family: Montserrat, sans-serif;
    font-weight: 500;
}

@media screen and (max-width: 768px) {
    .banner-title{
        text-align:left;
        font-size: 24px;
        font-weight: 400;
        padding:20px;
    }
    .banner{
        margin-top: 0px;
        margin-bottom: 20px;
        text-align: left;
        height: 120px;
    }
}
@media screen and (min-width:768px)and (max-width:992px) {
 
    .banner-title{
        font-size: 35px;    
    }
    .banner{
        height: 170px;
    }
}
